import { useEffect, useRef } from 'react';

export interface CursorGlowConfig {
  enabled?: boolean;
  size?: number;
  color?: string;
  opacity?: number;
  blur?: number;
  transitionDuration?: number;
  zIndex?: number;
  blendMode?: string;
  pulseOnClick?: boolean;
  pulseScale?: number;
  pulseDuration?: number;
  interactiveElements?: string; // CSS selector for elements that trigger stronger glow
  interactiveScale?: number;
  interactiveOpacity?: number;
  enableMobile?: boolean; // Enable touch support on mobile devices
  tapFadeDuration?: number; // How long the tap glow lasts on mobile
}

const DEFAULT_CONFIG: Required<CursorGlowConfig> = {
  enabled: true,
  size: 300,
  color: '#3182CE', // Chakra blue.500
  opacity: 0.3,
  blur: 60,
  transitionDuration: 200,
  zIndex: 9999,
  blendMode: 'normal',
  pulseOnClick: true,
  pulseScale: 1.5,
  pulseDuration: 400,
  interactiveElements: 'a, button, [role="button"], input, textarea, select',
  interactiveScale: 1.3,
  interactiveOpacity: 0.4,
  enableMobile: true,
  tapFadeDuration: 800,
};

export const useCursorGlow = (config: CursorGlowConfig = {}) => {
  const glowRef = useRef<HTMLDivElement | null>(null);
  const rafRef = useRef<number>();
  const cursorPos = useRef({ x: 0, y: 0 });
  const isInteracting = useRef(false);
  const fadeTimeoutRef = useRef<number>();

  const mergedConfig = { ...DEFAULT_CONFIG, ...config };

  useEffect(() => {
    if (!mergedConfig.enabled) return;

    // Create glow element
    const glowElement = document.createElement('div');
    glowElement.style.cssText = `
      position: fixed;
      pointer-events: none;
      width: ${mergedConfig.size}px;
      height: ${mergedConfig.size}px;
      border-radius: 50%;
      background: radial-gradient(circle, ${mergedConfig.color} 0%, transparent 70%);
      opacity: 0;
      filter: blur(${mergedConfig.blur}px);
      transform: translate(-50%, -50%);
      transition:
        width ${mergedConfig.transitionDuration}ms ease-out,
        height ${mergedConfig.transitionDuration}ms ease-out,
        opacity ${mergedConfig.transitionDuration}ms ease-out;
      z-index: ${mergedConfig.zIndex};
      mix-blend-mode: ${mergedConfig.blendMode};
      will-change: transform, opacity;
      left: -9999px;
      top: -9999px;
    `;
    document.body.appendChild(glowElement);
    glowRef.current = glowElement;

    // Mouse move handler with RAF for performance
    const handleMouseMove = (e: MouseEvent) => {
      cursorPos.current = { x: e.clientX, y: e.clientY };

      if (rafRef.current) {
        cancelAnimationFrame(rafRef.current);
      }

      rafRef.current = requestAnimationFrame(() => {
        if (glowRef.current) {
          glowRef.current.style.left = `${cursorPos.current.x}px`;
          glowRef.current.style.top = `${cursorPos.current.y}px`;
          glowRef.current.style.opacity = `${mergedConfig.opacity}`;
        }
      });
    };

    // Touch move handler for mobile
    const handleTouchMove = (e: TouchEvent) => {
      if (!mergedConfig.enableMobile) return;

      const touch = e.touches[0];
      if (!touch) return;

      cursorPos.current = { x: touch.clientX, y: touch.clientY };

      if (rafRef.current) {
        cancelAnimationFrame(rafRef.current);
      }

      rafRef.current = requestAnimationFrame(() => {
        if (glowRef.current) {
          glowRef.current.style.left = `${cursorPos.current.x}px`;
          glowRef.current.style.top = `${cursorPos.current.y}px`;
          glowRef.current.style.opacity = `${mergedConfig.opacity}`;
        }
      });

      // Clear existing fade timeout
      if (fadeTimeoutRef.current) {
        clearTimeout(fadeTimeoutRef.current);
      }

      // Fade out after touch ends
      fadeTimeoutRef.current = window.setTimeout(() => {
        if (glowRef.current) {
          glowRef.current.style.opacity = '0';
        }
      }, mergedConfig.tapFadeDuration);
    };

    // Touch start handler
    const handleTouchStart = (e: TouchEvent) => {
      if (!mergedConfig.enableMobile) return;

      const touch = e.touches[0];
      if (!touch) return;

      cursorPos.current = { x: touch.clientX, y: touch.clientY };

      if (glowRef.current) {
        glowRef.current.style.left = `${cursorPos.current.x}px`;
        glowRef.current.style.top = `${cursorPos.current.y}px`;
        glowRef.current.style.opacity = `${mergedConfig.opacity}`;
      }
    };

    // Touch end handler
    const handleTouchEnd = () => {
      if (!mergedConfig.enableMobile) return;

      // Clear existing fade timeout
      if (fadeTimeoutRef.current) {
        clearTimeout(fadeTimeoutRef.current);
      }

      // Fade out glow
      fadeTimeoutRef.current = window.setTimeout(() => {
        if (glowRef.current) {
          glowRef.current.style.opacity = '0';
        }
      }, mergedConfig.tapFadeDuration);
    };

    // Interactive element hover handlers
    const handleInteractiveEnter = () => {
      if (!glowRef.current || isInteracting.current) return;
      isInteracting.current = true;

      glowRef.current.style.width = `${mergedConfig.size * mergedConfig.interactiveScale}px`;
      glowRef.current.style.height = `${mergedConfig.size * mergedConfig.interactiveScale}px`;
      glowRef.current.style.opacity = `${mergedConfig.interactiveOpacity}`;
    };

    const handleInteractiveLeave = () => {
      if (!glowRef.current || !isInteracting.current) return;
      isInteracting.current = false;

      glowRef.current.style.width = `${mergedConfig.size}px`;
      glowRef.current.style.height = `${mergedConfig.size}px`;
      glowRef.current.style.opacity = `${mergedConfig.opacity}`;
    };

    // Click pulse effect
    const handleClick = () => {
      if (!mergedConfig.pulseOnClick || !glowRef.current) return;

      const originalWidth = glowRef.current.style.width;
      const originalHeight = glowRef.current.style.height;
      const originalOpacity = glowRef.current.style.opacity;

      glowRef.current.style.transition = `
        width ${mergedConfig.pulseDuration / 2}ms ease-out,
        height ${mergedConfig.pulseDuration / 2}ms ease-out,
        opacity ${mergedConfig.pulseDuration / 2}ms ease-out
      `;

      glowRef.current.style.width = `${mergedConfig.size * mergedConfig.pulseScale}px`;
      glowRef.current.style.height = `${mergedConfig.size * mergedConfig.pulseScale}px`;
      glowRef.current.style.opacity = `${mergedConfig.opacity * 1.5}`;

      setTimeout(() => {
        if (!glowRef.current) return;
        glowRef.current.style.width = originalWidth;
        glowRef.current.style.height = originalHeight;
        glowRef.current.style.opacity = originalOpacity;

        setTimeout(() => {
          if (!glowRef.current) return;
          glowRef.current.style.transition = `
            width ${mergedConfig.transitionDuration}ms ease-out,
            height ${mergedConfig.transitionDuration}ms ease-out,
            opacity ${mergedConfig.transitionDuration}ms ease-out
          `;
        }, mergedConfig.pulseDuration / 2);
      }, mergedConfig.pulseDuration / 2);
    };

    // Attach event listeners
    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('click', handleClick);

    // Touch event listeners for mobile
    if (mergedConfig.enableMobile) {
      document.addEventListener('touchstart', handleTouchStart, { passive: true });
      document.addEventListener('touchmove', handleTouchMove, { passive: true });
      document.addEventListener('touchend', handleTouchEnd);
      document.addEventListener('touchcancel', handleTouchEnd);
    }

    // Add interactive element listeners
    const interactiveElements = document.querySelectorAll(mergedConfig.interactiveElements);
    interactiveElements.forEach(el => {
      el.addEventListener('mouseenter', handleInteractiveEnter);
      el.addEventListener('mouseleave', handleInteractiveLeave);
    });

    // Use MutationObserver to handle dynamically added elements
    const observer = new MutationObserver(() => {
      const newElements = document.querySelectorAll(mergedConfig.interactiveElements);
      newElements.forEach(el => {
        el.addEventListener('mouseenter', handleInteractiveEnter);
        el.addEventListener('mouseleave', handleInteractiveLeave);
      });
    });

    observer.observe(document.body, { childList: true, subtree: true });

    // Cleanup
    return () => {
      if (rafRef.current) {
        cancelAnimationFrame(rafRef.current);
      }

      if (fadeTimeoutRef.current) {
        clearTimeout(fadeTimeoutRef.current);
      }

      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('click', handleClick);

      if (mergedConfig.enableMobile) {
        document.removeEventListener('touchstart', handleTouchStart);
        document.removeEventListener('touchmove', handleTouchMove);
        document.removeEventListener('touchend', handleTouchEnd);
        document.removeEventListener('touchcancel', handleTouchEnd);
      }

      const elements = document.querySelectorAll(mergedConfig.interactiveElements);
      elements.forEach(el => {
        el.removeEventListener('mouseenter', handleInteractiveEnter);
        el.removeEventListener('mouseleave', handleInteractiveLeave);
      });

      observer.disconnect();

      if (glowRef.current && document.body.contains(glowRef.current)) {
        document.body.removeChild(glowRef.current);
        glowRef.current = null;
      }
    };
  }, [mergedConfig]);

  return null;
};