import { useAuroraTrail, AuroraTrailConfig } from 'utils/useAuroraTrail';

interface AuroraTrailProps {
  config?: AuroraTrailConfig;
}

/**
 * AuroraTrail Component - Creates a magical aurora trail effect that follows the cursor
 *
 * Features:
 * - Beautiful gradient trail with multiple colors
 * - Sparkle particles along the trail
 * - Smooth fade-out animation
 * - Works on both desktop (mouse) and mobile (touch)
 * - Fully customizable colors and behavior
 *
 * @example
 * ```tsx
 * // Basic usage with default config
 * <AuroraTrail />
 *
 * // Custom purple/pink gradient
 * <AuroraTrail config={{
 *   colors: ['#8B5CF6', '#EC4899', '#F97316'],
 *   trailLength: 30,
 *   enableParticles: true,
 * }} />
 *
 * // Disable on mobile
 * <AuroraTrail config={{
 *   enableMobile: false
 * }} />
 * ```
 */
export const AuroraTrail = ({ config }: AuroraTrailProps) => {
  useAuroraTrail(config);
  return null;
};