import { useEffect } from 'react';
import { AI_CHAT_CONFIG } from './constants';

/**
 * Custom hook to load AI chat embed script
 */
export const useAIChat = () => {
  useEffect(() => {
    // Only load if chat is enabled
    if (!AI_CHAT_CONFIG.enabled) {
      return;
    }

    // Function to load the AI chat script
    const loadAIChatScript = () => {
      try {
        // Check if script already exists
        if (document.getElementById('aiChatEmbedScript')) {
          console.log('AI Chat script already loaded');
          return;
        }

        const embedUrl = AI_CHAT_CONFIG.embedScriptUrl;

        if (embedUrl && embedUrl.trim() !== '') {
          const script = document.createElement('script');
          script.id = 'aiChatEmbedScript';
          script.defer = true;
          script.src = embedUrl;

          script.onerror = function(event) {
            console.error('Failed to load AI Chat embed script:', embedUrl);
            console.error('Error details:', event);
            console.warn('This might be due to network issues, CORS policy, or the script being temporarily unavailable');

            // Try loading the script without parameters as fallback
            const baseScriptUrl = AI_CHAT_CONFIG.embedScriptMainSrc;
            if (embedUrl !== baseScriptUrl) {
              console.log('Attempting to load script without parameters as fallback...');
              const fallbackScript = document.createElement('script');
              fallbackScript.id = 'aiChatEmbedScriptFallback';
              fallbackScript.defer = true;
              fallbackScript.src = baseScriptUrl;
              fallbackScript.onload = function() {
                console.log('AI Chat embed script loaded successfully (fallback)');
              };
              fallbackScript.onerror = function() {
                console.error('Fallback script loading also failed');
              };
              document.head.appendChild(fallbackScript);
            }
          };

          script.onload = function() {
            console.log('AI Chat embed script loaded successfully');
          };

          document.head.appendChild(script);
        } else {
          console.warn('AI Chat embed script URL is empty or invalid');
        }
      } catch (error) {
        console.error('Error loading AI Chat embed script:', error);
      }
    };

    // Load the script when the component mounts
    loadAIChatScript();

    // Cleanup function to remove scripts when component unmounts
    return () => {
      const existingScript = document.getElementById('aiChatEmbedScript');
      const fallbackScript = document.getElementById('aiChatEmbedScriptFallback');
      if (existingScript) {
        existingScript.remove();
      }
      if (fallbackScript) {
        fallbackScript.remove();
      }
    };
  }, []);
};