import { useCursorGlow, CursorGlowConfig } from 'utils/useCursorGlow';

interface CursorGlowProps {
  config?: CursorGlowConfig;
}

/**
 * CursorGlow Component - Adds an interactive glow effect that follows the cursor
 *
 * @example
 * ```tsx
 * // Basic usage with default config
 * <CursorGlow />
 *
 * // Custom configuration
 * <CursorGlow config={{
 *   enabled: true,
 *   size: 400,
 *   color: '#FF6B6B',
 *   opacity: 0.2,
 *   blur: 100,
 *   pulseOnClick: true,
 * }} />
 *
 * // Disable on mobile/small screens
 * <CursorGlow config={{
 *   enabled: window.innerWidth > 768
 * }} />
 * ```
 */
export const CursorGlow = ({ config }: CursorGlowProps) => {
  useCursorGlow(config);
  return null;
};