import { useEffect, useRef } from 'react';

export interface AuroraTrailConfig {
  enabled?: boolean;
  colors?: string[]; // Gradient colors for aurora
  trailLength?: number; // Number of trail points
  lineWidth?: number; // Base thickness of aurora line
  trailOpacity?: number; // Base opacity
  fadeSpeed?: number; // How fast trail fades (0-1)
  blur?: number; // Blur amount
  zIndex?: number;
  enableParticles?: boolean; // Sparkle particles
  particleCount?: number; // Particles per frame
  particleSize?: number; // Size of sparkle particles
  particleLife?: number; // Particle lifetime in ms
  enableMobile?: boolean; // Touch support
  smoothing?: number; // Curve smoothing (0-1)
  trailCount?: number; // Number of parallel trails
  deviationAmount?: number; // How much trails deviate from cursor
}

interface TrailPoint {
  x: number;
  y: number;
  timestamp: number;
  opacity: number;
}

interface Particle {
  x: number;
  y: number;
  size: number;
  opacity: number;
  createdAt: number;
  velocityX: number;
  velocityY: number;
}

const DEFAULT_CONFIG: Required<AuroraTrailConfig> = {
  enabled: true,
  colors: ['#8B5CF6', '#EC4899', '#3B82F6'], // Purple, pink, blue
  trailLength: 50,
  lineWidth: 2.5,
  trailOpacity: 0.8,
  fadeSpeed: 0.975,
  blur: 14,
  zIndex: 9998,
  enableParticles: true,
  particleCount: 1,
  particleSize: 2.5,
  particleLife: 700,
  enableMobile: true,
  smoothing: 0.25,
  trailCount: 5,
  deviationAmount: 30,
};

interface Trail {
  points: TrailPoint[];
  offset: { x: number; y: number };
  velocity: { x: number; y: number }; // Current velocity for momentum
  acceleration: { x: number; y: number }; // Acceleration towards target
  width: number;
  opacity: number;
  colorIndex: number;
  waveAmplitude: number; // Wave height for curvature
  waveFrequency: number; // Wave speed
  wavePhase: number; // Current wave position
  curvatureStyle: 'sine' | 'spiral' | 'zigzag' | 'smooth';
  mass: number; // Mass for physics (heavier = more momentum)
  friction: number; // Friction/damping
}

export const useAuroraTrail = (config: AuroraTrailConfig = {}) => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const trails = useRef<Trail[]>([]);
  const particles = useRef<Particle[]>([]);
  const rafRef = useRef<number>();

  useEffect(() => {
    const mergedConfig = { ...DEFAULT_CONFIG, ...config };

    if (!mergedConfig.enabled) return;

    // Initialize multiple trails with different curvature styles and physics
    const curvatureStyles: Array<'sine' | 'spiral' | 'zigzag' | 'smooth'> = ['sine', 'spiral', 'zigzag', 'smooth', 'sine'];

    trails.current = Array.from({ length: mergedConfig.trailCount }, (_, i) => ({
      points: [],
      offset: {
        x: (Math.random() - 0.5) * mergedConfig.deviationAmount,
        y: (Math.random() - 0.5) * mergedConfig.deviationAmount,
      },
      velocity: { x: 0, y: 0 }, // Initial velocity
      acceleration: { x: 0, y: 0 }, // Initial acceleration
      width: mergedConfig.lineWidth * (0.6 + Math.random() * 1.2), // Varying widths
      opacity: 0.5 + Math.random() * 0.4, // Varying opacity
      colorIndex: i % mergedConfig.colors.length,
      waveAmplitude: 5 + Math.random() * 15, // Wave height variation
      waveFrequency: 0.02 + Math.random() * 0.03, // Wave speed
      wavePhase: Math.random() * Math.PI * 2, // Random starting phase
      curvatureStyle: curvatureStyles[i % curvatureStyles.length],
      mass: 0.8 + Math.random() * 0.4, // Mass variation (0.8 - 1.2)
      friction: 0.85 + Math.random() * 0.1, // Friction variation (0.85 - 0.95)
    }));

    // Create canvas
    const canvas = document.createElement('canvas');
    canvas.style.cssText = `
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      pointer-events: none;
      z-index: ${mergedConfig.zIndex};
    `;
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
    document.body.appendChild(canvas);
    canvasRef.current = canvas;

    const ctx = canvas.getContext('2d', { alpha: true });
    if (!ctx) return;

    // Handle resize
    const handleResize = () => {
      if (canvasRef.current) {
        canvasRef.current.width = window.innerWidth;
        canvasRef.current.height = window.innerHeight;
      }
    };

    window.addEventListener('resize', handleResize);

    // Add trail point to all trails with physics-based momentum
    const addTrailPoint = (x: number, y: number) => {
      const now = Date.now();

      // Add points to each trail with physics simulation
      trails.current.forEach((trail) => {
        // Update wave phase for animation
        trail.wavePhase += trail.waveFrequency;

        // Calculate curvature offset based on style
        let curvatureX = 0;
        let curvatureY = 0;

        switch (trail.curvatureStyle) {
          case 'sine':
            // Smooth sine wave
            curvatureX = Math.sin(trail.wavePhase) * trail.waveAmplitude;
            curvatureY = Math.cos(trail.wavePhase * 0.7) * trail.waveAmplitude * 0.5;
            break;

          case 'spiral':
            // Spiral outward pattern
            const spiralRadius = (trail.wavePhase % (Math.PI * 2)) / (Math.PI * 2) * trail.waveAmplitude;
            curvatureX = Math.cos(trail.wavePhase * 2) * spiralRadius;
            curvatureY = Math.sin(trail.wavePhase * 2) * spiralRadius;
            break;

          case 'zigzag':
            // Sharp zigzag pattern
            curvatureX = (Math.sin(trail.wavePhase * 3) > 0 ? 1 : -1) * trail.waveAmplitude * 0.8;
            curvatureY = (Math.cos(trail.wavePhase * 2.5) > 0 ? 1 : -1) * trail.waveAmplitude * 0.5;
            break;

          case 'smooth':
            // Multiple sine waves combined
            curvatureX = Math.sin(trail.wavePhase) * trail.waveAmplitude * 0.6 +
                        Math.sin(trail.wavePhase * 2) * trail.waveAmplitude * 0.3;
            curvatureY = Math.cos(trail.wavePhase * 1.3) * trail.waveAmplitude * 0.4;
            break;
        }

        // Calculate target position with base offset + curvature
        const targetX = x + trail.offset.x + curvatureX;
        const targetY = y + trail.offset.y + curvatureY;

        // Get current position
        const lastPoint = trail.points[0];
        const currentX = lastPoint ? lastPoint.x : targetX;
        const currentY = lastPoint ? lastPoint.y : targetY;

        // Calculate acceleration (spring force towards target)
        const springStrength = 0.08 / trail.mass; // Heavier trails = slower response
        trail.acceleration.x = (targetX - currentX) * springStrength;
        trail.acceleration.y = (targetY - currentY) * springStrength;

        // Update velocity with acceleration
        trail.velocity.x += trail.acceleration.x;
        trail.velocity.y += trail.acceleration.y;

        // Apply friction/damping
        trail.velocity.x *= trail.friction;
        trail.velocity.y *= trail.friction;

        // Update position with velocity (momentum/inertia creates overshoot)
        const actualX = currentX + trail.velocity.x;
        const actualY = currentY + trail.velocity.y;

        trail.points.unshift({
          x: actualX,
          y: actualY,
          timestamp: now,
          opacity: trail.opacity * mergedConfig.trailOpacity,
        });

        // Limit trail length
        if (trail.points.length > mergedConfig.trailLength) {
          trail.points.pop();
        }

        // Slowly drift the base offset for organic variation
        trail.offset.x += (Math.random() - 0.5) * 0.3;
        trail.offset.y += (Math.random() - 0.5) * 0.3;

        // Keep offset within bounds
        const maxOffset = mergedConfig.deviationAmount;
        trail.offset.x = Math.max(-maxOffset, Math.min(maxOffset, trail.offset.x));
        trail.offset.y = Math.max(-maxOffset, Math.min(maxOffset, trail.offset.y));
      });

      // Add sparkle particles occasionally
      if (mergedConfig.enableParticles && Math.random() > 0.7) {
        for (let i = 0; i < mergedConfig.particleCount; i++) {
          particles.current.push({
            x: x + (Math.random() - 0.5) * 10,
            y: y + (Math.random() - 0.5) * 10,
            size: mergedConfig.particleSize * (0.5 + Math.random() * 0.5),
            opacity: 1,
            createdAt: now,
            velocityX: (Math.random() - 0.5) * 0.5,
            velocityY: (Math.random() - 0.5) * 0.5,
          });
        }
      }
    };

    // Mouse move handler
    const handleMouseMove = (e: MouseEvent) => {
      addTrailPoint(e.clientX, e.clientY);
    };

    // Touch handlers
    const handleTouchStart = (e: TouchEvent) => {
      if (!mergedConfig.enableMobile) return;
      const touch = e.touches[0];
      if (touch) {
        addTrailPoint(touch.clientX, touch.clientY);
      }
    };

    const handleTouchMove = (e: TouchEvent) => {
      if (!mergedConfig.enableMobile) return;
      const touch = e.touches[0];
      if (touch) {
        addTrailPoint(touch.clientX, touch.clientY);
      }
    };

    // Render loop
    const render = () => {
      if (!ctx || !canvasRef.current) return;

      // Clear canvas
      ctx.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height);

      const now = Date.now();

      // Render all trails
      trails.current.forEach((trail, trailIndex) => {
        if (trail.points.length < 2) return;

        // Fade all points
        trail.points.forEach((point) => {
          point.opacity *= mergedConfig.fadeSpeed;
        });

        // Clean up old points
        trail.points = trail.points.filter((point) => point.opacity > 0.01);

        if (trail.points.length < 2) return;

        // Draw smooth curved line
        ctx.save();

        // Create gradient along the trail
        const startPoint = trail.points[0];
        const endPoint = trail.points[trail.points.length - 1];

        const gradient = ctx.createLinearGradient(
          startPoint.x,
          startPoint.y,
          endPoint.x,
          endPoint.y
        );

        // Use different color combinations for each trail
        const colorStart = mergedConfig.colors[trail.colorIndex];
        const colorEnd = mergedConfig.colors[(trail.colorIndex + 1) % mergedConfig.colors.length];

        gradient.addColorStop(0, colorStart);
        gradient.addColorStop(0.5, mergedConfig.colors[(trail.colorIndex + 1) % mergedConfig.colors.length]);
        gradient.addColorStop(1, colorEnd);

        ctx.strokeStyle = gradient;
        ctx.lineWidth = trail.width;
        ctx.lineCap = 'round';
        ctx.lineJoin = 'round';
        ctx.shadowBlur = mergedConfig.blur * (0.5 + trailIndex * 0.2);
        ctx.shadowColor = colorStart;

        // Draw main line with varying opacity
        ctx.globalAlpha = trail.points[0].opacity * trail.opacity;
        ctx.beginPath();
        ctx.moveTo(trail.points[0].x, trail.points[0].y);

        // Use quadratic curves for smooth flowing effect
        for (let i = 1; i < trail.points.length - 1; i++) {
          const point = trail.points[i];
          const nextPoint = trail.points[i + 1];

          const xc = (point.x + nextPoint.x) / 2;
          const yc = (point.y + nextPoint.y) / 2;

          ctx.quadraticCurveTo(point.x, point.y, xc, yc);
        }

        // Draw to last point
        const lastPoint = trail.points[trail.points.length - 1];
        ctx.lineTo(lastPoint.x, lastPoint.y);
        ctx.stroke();

        // Draw subtle glow layer for thicker trails
        if (trail.width > mergedConfig.lineWidth) {
          ctx.globalAlpha = trail.points[0].opacity * trail.opacity * 0.2;
          ctx.lineWidth = trail.width * 2;
          ctx.shadowBlur = mergedConfig.blur * 2;
          ctx.stroke();
        }

        ctx.restore();
      });

      // Update and render sparkle particles
      if (mergedConfig.enableParticles) {
        particles.current = particles.current.filter((particle) => {
          const age = now - particle.createdAt;
          if (age > mergedConfig.particleLife) return false;

          // Update particle position
          particle.x += particle.velocityX;
          particle.y += particle.velocityY;

          // Fade out
          const lifeProgress = age / mergedConfig.particleLife;
          particle.opacity = 1 - lifeProgress;

          // Render sparkle particle
          ctx.save();
          ctx.globalAlpha = particle.opacity;
          ctx.shadowBlur = 8;
          ctx.shadowColor = '#FFFFFF';

          // Draw small star/sparkle shape
          const particleGradient = ctx.createRadialGradient(
            particle.x,
            particle.y,
            0,
            particle.x,
            particle.y,
            particle.size * 2
          );
          particleGradient.addColorStop(0, '#FFFFFF');
          particleGradient.addColorStop(0.3, mergedConfig.colors[Math.floor(Math.random() * mergedConfig.colors.length)]);
          particleGradient.addColorStop(1, 'transparent');

          ctx.fillStyle = particleGradient;
          ctx.beginPath();
          ctx.arc(particle.x, particle.y, particle.size, 0, Math.PI * 2);
          ctx.fill();

          // Add a tiny bright center
          ctx.fillStyle = 'rgba(255, 255, 255, ' + particle.opacity + ')';
          ctx.beginPath();
          ctx.arc(particle.x, particle.y, particle.size * 0.3, 0, Math.PI * 2);
          ctx.fill();

          ctx.restore();

          return true;
        });
      }

      rafRef.current = requestAnimationFrame(render);
    };

    // Start animation loop
    rafRef.current = requestAnimationFrame(render);

    // Attach event listeners
    document.addEventListener('mousemove', handleMouseMove);

    if (mergedConfig.enableMobile) {
      document.addEventListener('touchstart', handleTouchStart, { passive: true });
      document.addEventListener('touchmove', handleTouchMove, { passive: true });
    }

    // Cleanup
    return () => {
      if (rafRef.current) {
        cancelAnimationFrame(rafRef.current);
      }

      document.removeEventListener('mousemove', handleMouseMove);

      if (mergedConfig.enableMobile) {
        document.removeEventListener('touchstart', handleTouchStart);
        document.removeEventListener('touchmove', handleTouchMove);
      }

      window.removeEventListener('resize', handleResize);

      if (canvasRef.current && document.body.contains(canvasRef.current)) {
        document.body.removeChild(canvasRef.current);
        canvasRef.current = null;
      }

      trails.current = [];
      particles.current = [];
    };
  }, [config]);

  return null;
};